@import './variable.scss';

/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {

    .contact {
        width: 100%;
        height: 100%;
        padding: 50px;
        padding-left: 10%;
        padding-right: 10%;
        text-align: center;

        &__container {
            display: flex;
            flex-flow: row;

        }

        &__item {
            color: #fff;
            flex-basis: 50%;

            &:nth-child(1) {
                display: none;
            }
        }

        &__title {
            height: 50%;
        }

        &__text {
            height: 50%;
            font-size: 1.2rem;
            font-weight: 400;
            padding-top: 25px;
        }
    }

}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

/* 큰화면 데스크탑 (가로 해상도가 1200px 보다 큰 화면에 적용) */
@media (min-width: 1200px) {

    .contact {

        &__item {
            color: #fff;
            flex-basis: 33.33%;

            &:nth-child(1) {
                display: block;
            }
        }

        &__title {
            height: 50%;
            padding-top: 100px;
        }

        &__text {
            height: 50%;
            font-size: 2rem;
            font-weight: 400;
            padding-top: 0px;
        }

    }
}