@import './variable.scss';

.detail-container {
    text-rendering: unset;

    .main-title {
        text-align: center;
        font-size: 2.3rem;
        font-weight: 700;
        margin-bottom: 20px;
        min-height: 36px;

        // &::after {
        //     content: "";
        //     position: absolute;
        //     width: 100px;
        //     top: 61px;
        //     left: 40%;
        //     border-bottom: 3px solid #df405a;
        // }
    }

    .sub-title {
        text-align: left;
        font-size: 1.2rem;
        font-style: normal;
        min-height: 19px;
        padding: 10px;
        border-radius: 5px;
        font-weight: 500;
        background-color: #ffe4e4;
    }

    ul {
        margin-top: 20px;

        li {
            display: flex;
            flex-flow: row;

            .key {
                font-weight: 500;
                font-size: 1.4rem;
                width: 28%;
                min-width: 28%;
            }

            .value {
                font-size: 1.2rem;
                width: 72%;
                max-width: 72%;
            }
        }
    }

}