@import './variable.scss';



/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {
    .intro-container {
        margin-right: auto;
        margin-left: auto;
        height: 100%;
        display: flex;
        flex-flow: row;

        .col {
            width: 25%;
            opacity: 0.9;
            color: #fff;
            text-align: center;
            padding-top: 50px;
            padding-left: 5px;
            padding-right: 5px;

            &:nth-child(1n) {
                background: #111;
            }

            &:nth-child(2n) {
                background: #222;
            }

            p {
                font-size: 1.2rem;
                -webkit-font-smoothing: initial;

                &.img {
                    display: none;
                    margin-bottom: 50px;
                }
            }
        }
    }
}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

/* 큰화면 데스크탑 (가로 해상도가 1200px 보다 큰 화면에 적용) */
@media (min-width: 1200px) {
    .intro-container {

        .col {
            padding-top: 100px;
            padding-bottom: 100px;
            padding-left: 15px;
            padding-right: 15px;

            p {
                font-size: 1.6rem;

                &.img {
                    display: block;
                    margin-bottom: 50px;
                }
            }
        }
    }
}