@import './variable.scss';

/* 모달 */

/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {
    .custom-modal-root {
        display: none;
        font-family: $basic-font;

        & .custom-modal-background-root {
            z-index: 4;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            position: fixed;
            align-items: center;
            justify-content: center;
            background-color: rgba(0, 0, 0, 0.75);
            -webkit-tap-highlight-color: transparent;
            transition: none;
        }

        &.open {
            overflow: hidden;
            display: block;

            .custom-modal-frame {
                overflow-y: auto;
                z-index: 5;
                border-radius: 4px;
                animation-name: animation-fade-in;
                animation-duration: 0.3s;
                position: fixed;
                box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14),
                    0px 1px 14px 0px rgba(0, 0, 0, 0.12);
                background-color: #454443;
                top: 50%;
                left: 0px;
                width: 100% !important;
                height: 100% !important;
                -webkit-transform: translate(0%, -50%);
                -ms-transform: translate(0%, -50%);
                -moz-transform: translate(0%, -50%);
                -o-transform: translate(0%, -50%);
                transform: translate(0%, -50%);
                border: 2px solid #454443;

                &>svg {
                    visibility: visible;
                    float: right;
                    cursor: pointer;
                }


                .custom-modal-header {
                    display: block;
                    height: 60px;
                    background-color: #454443;
                    border-top-left-radius: 1px;
                    border-top-right-radius: 1px;
                    padding: 20px 10px;


                    .circle {
                        display: flex;

                        li {
                            width: 20px;
                            height: 20px;
                            border-radius: 50%;
                            margin-right: 10px;

                            &.red {
                                background-color: #fb5b57;
                                cursor: pointer;
                            }

                            &.yellow {
                                background-color: #e3bf44;
                            }

                            &.green {
                                background-color: #57c041;
                            }
                        }
                    }

                }

                .custom-modal-contents {
                    height: calc(100% - 60px);
                    width: 100%;
                    padding: 20px;
                    background-color: #fff;
                }
            }
        }
    }
}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

/* 큰화면 데스크탑 (가로 해상도가 1200px 보다 큰 화면에 적용) */
@media (min-width: 1200px) {
    .custom-modal-root {
        &.open {

            .custom-modal-frame {
                width: 50% !important;
                height: inherit !important;
                background-color: #454443;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
            }
        }
    }
}