@import './variable.scss';



/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {
    .contents-container {
        h2 {
            position: relative;
            margin-top: 200px;
            margin-bottom: 50px;

            span {
                color: $sub-secondary;
            }

            &::after {
                content: '';
                position: absolute;
                width: 60px;
                top: 46px;
                left: calc(50% - 25px);
                border-bottom: 3px solid $tertiary;
            }
        }

        .controls-portfolio {
            display: inline-flex;

            ul {
                display: flex;
                flex-flow: column;

                li {
                    float: left;
                    font-weight: 500;
                    font-size: 1.4rem;
                    text-transform: uppercase;
                    letter-spacing: 0.5px;
                    cursor: pointer;

                    a {
                        color: $sub-tertiary;
                        border-color: $sub-primary;
                        transition: all ease 0.5s;
                        padding: 5px 18px;
                        position: relative;

                        :visited,
                        :link {
                            text-decoration: none;
                            color: $tertiary;
                        }
                    }

                    &.on>a {
                        color: $tertiary;
                    }

                    &:hover>a {
                        color: $sub-primary;

                        &::before {
                            bottom: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            border-left: 2px solid;
                            border-bottom: 2px solid;
                            border-color: inherit;
                            opacity: 1;
                        }

                        &::after {
                            top: 0px;
                            right: 0px;
                            width: 100%;
                            height: 100%;
                            border-right: 2px solid;
                            border-top: 2px solid;
                            border-color: inherit;
                            opacity: 1;
                        }
                    }
                }
            }
        }

        .list-portfolio {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-around;
            margin-bottom: 200px;

            .item {
                position: relative;
                cursor: not-allowed;
                flex-basis: 260px;
                min-width: 260px;
                margin: 25px;
                opacity: 0.5;
                transition: ease 0.3s;
                filter: blur(3px);

                .window {
                    display: block;
                    height: 180px;
                    border: 2px solid #454443;
                    border-radius: 4px;
                    box-shadow: antiquewhite;
                    box-shadow: 5px 5px 10px #ccc;

                    &-header {
                        display: block;
                        height: 30px;
                        background-color: #454443;
                        border-top-left-radius: 1px;
                        border-top-right-radius: 1px;
                        padding: 10px 5px;

                        .circle {
                            display: flex;

                            li {
                                width: 10px;
                                height: 10px;
                                border-radius: 50%;
                                margin-right: 5px;

                                &.red {
                                    background-color: #fb5b57;
                                }

                                &.yellow {
                                    background-color: #e3bf44;
                                }

                                &.green {
                                    background-color: #57c041;
                                }
                            }
                        }
                    }

                    &-body {
                        height: calc(100% - 30px);

                        svg {
                            top: 30%;
                            position: relative;

                            &.img-close {
                                display: inline-block;
                                z-index: 4;
                            }

                            &.img-open {
                                display: none;
                            }

                        }
                    }
                }

                &.visible {
                    cursor: pointer;
                    opacity: 1;
                    filter: blur(0px);

                    &:hover {
                        .img-close {
                            display: none;
                        }

                        .img-open {
                            display: inline-block;
                            z-index: 4;
                        }
                    }
                }

                p {
                    font-size: 1.4rem;
                    font-weight: bold;
                    padding: 0px 10px;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    overflow: hidden;

                    &.image {
                        margin-top: 35px;
                    }

                    &.text {
                        margin-top: 10px;

                        span {
                            font-size: 1.6rem;
                            color: $tertiary;
                        }
                    }
                }
            }
        }
    }
}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

