@import './variable.scss';

.pulse {
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 100rem;

    circle {
        fill: #ff5154;
        transform: scale(0);
        opacity: 0;
        transform-origin: 50% 50%;
        animation: pulse 3s cubic-bezier(.5, .5, 0, 1);
        animation-iteration-count: infinite;

        &:nth-child(2) {
            fill: #7fc6a4;
            animation: pulse 3s 1.5s cubic-bezier(.5, .5, 0, 1);
            animation-iteration-count: infinite;
        }

        &:nth-child(3) {
            fill: #e5f77d;
            animation: pulse 3s 2.25s cubic-bezier(.5, .5, 0, 1);
            animation-iteration-count: infinite;
        }

    }

}

@keyframes pulse {
    25% {
        opacity: 0.4;
    }

    100% {
        transform: scale(1);
    }

}