@import './variable.scss';



/* 가로모드 모바일 디바이스 (가로 해상도가 100px 보다 큰 화면에 적용) */
@media (min-width: 100px) {
    .history-container {
        h2 {
            position: relative;
            margin-top: 100px;
            margin-bottom: 50px;

            span {
                color: $sub-secondary;
            }

            &::after {
                content: '';
                position: absolute;
                width: 60px;
                top: 46px;
                left: calc(50% - 25px);
                border-bottom: 3px solid $tertiary;
            }
        }

        ul.timeLine {
            position: relative;

            &::before {
                content: "";
                position: absolute;
                left: 10px;
                border-left: 1px solid #df405a;
                height: 100%;
                z-index: 1;
            }

            li.item {
                display: block;
                position: relative;
                min-height: 250px;
                margin-bottom: 50px;

                div.panel {
                    width: 50%;

                    dl {
                        width: 100%;

                        dt {
                            font-weight: 900;
                            font-size: 1.2rem;
                        }

                        dd {

                            &.title {
                                font-weight: 400;
                                font-size: 1rem;
                            }

                            &.desc {
                                font-size: 1.2rem;
                            }
                        }
                    }

                    &::after {
                        content: "";
                        left: 1px;
                        position: absolute;
                        border: 1px solid #fec8c9;
                        border-radius: 100%;
                        background-color: #df405a;
                        top: 11%;
                        padding: 8px;
                        z-index: 2;
                        box-shadow: 0px 0px 8px 8px #fec8c9;
                    }
                }

                &:nth-child(1n) {
                    div.panel {
                        text-align: left;

                        dl {
                            text-align: left;
                            margin-left: 80px;
                            float: left;
                        }
                    }
                }

                &:nth-child(2n) {
                    div.panel {
                        float: left;

                        dl {
                            text-align: left;
                            margin-left: 80px;
                            float: left;
                        }
                    }
                }
            }
        }

        .btn-area {
            width: 100%;
            margin-top: -50px;
            text-align: left;
            margin-left: -30px;

            .btn-more-history {
                margin-left: 10px;
                border: 2px solid $tertiary;
                font-size: 1.4rem;
                padding: 10px;
                display: inline-block;
                font-weight: 600;
                background-color: transparent;
                color: $tertiary;
                transition: background-color 0.3s;
                display: none;

                &.view {
                    display: inline-block;
                }

                &:hover {
                    background-color: $tertiary;
                    color: #fff;
                }
            }
        }
    }

}

/* 태블릿 디바이스 (가로 해상도가 768px 보다 큰 화면에 적용) */
@media (min-width: 768px) {}

/* 테스크탑 (가로 해상도가 992px 보다 큰 화면에 적용) */
@media (min-width: 992px) {}

/* 큰화면 데스크탑 (가로 해상도가 1200px 보다 큰 화면에 적용) */
@media (min-width: 1200px) {

    .history-container {
        h2 {
            margin-top: 200px;
        }

        ul.timeLine {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: 50%;
                border-left: 1px solid $tertiary;
                height: 100%;
                z-index: 1;
            }

            li.item {
                div.panel {

                    dl {

                        dd {

                            &.title {
                                font-size: 1.2rem;
                            }
                        }

                    }

                    &::after {
                        content: '';
                        left: calc(50% - 15px);
                        position: absolute;
                        border: 8px solid $sub-primary;
                        border-radius: 100%;
                        background-color: $tertiary;
                        top: 11%;
                        padding: 8px;
                        z-index: 2;
                        box-shadow: 0px 0px 8px 8px $sub-primary;
                    }
                }

                &:nth-child(1n) {
                    div.panel {
                        text-align: left;

                        dl {
                            text-align: right;
                            margin-right: 80px;
                            float: right;
                        }
                    }
                }

                &:nth-child(2n) {
                    div.panel {
                        float: right;

                        dl {
                            text-align: left;
                            margin-left: 80px;
                            float: left;
                        }
                    }
                }
            }
        }

        .btn-area {
            width: 100%;
            margin-top: -50px;
            text-align: center;
            margin-left: 0px;

            .btn-more-history {
                font-size: 1.2rem;
                padding: 10px;
                margin-left: 0px;
                width: 100px;

                &:hover {
                    background-color: $tertiary;
                    color: #fff;
                }
            }
        }
    }
}